package pro.rent.navigation

import pro.rent.models.Constants.CAR_ID_PARAM
import kotlin.uuid.ExperimentalUuidApi
import kotlin.uuid.Uuid

sealed class Screen(val route: String) {
    data object HomePage : Screen(route = "/")

    data object CarDetailsPage : Screen(route = "/details/") {
        @ExperimentalUuidApi
        fun passCar(carId: Uuid) = "/details?${CAR_ID_PARAM}=$carId"
    }
}